import React, { useState, useEffect } from "react";
import Logo from "../Reusable/Logo";
import "./index.css";

export default function Navbar() {
  const [toggled, setToggled] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  let navHeight = window.innerWidth <= 767 ? "0" : "15vh";
  useEffect(() => {
    window.onscroll = () => {
      if (window.innerWidth >= 767) {
        if (
          document.body.scrollTop >= 100 ||
          document.documentElement.scrollTop >= 100
        ) {
          setScrolled(true);
        } else if (
          document.body.scrollTop <= 100 ||
          document.documentElement.scrollTop <= 100
        ) {
          setScrolled(false);
        }
      }
    };
  }, []);
  return (
    <nav
      className="Navbar"
      style={
        scrolled
          ? {
              backgroundColor: "#222A36",
              boxShadow: "0 2px 4px 6px #00000030",
            }
          : { backgroundColor: "transparent" }
      }
    >
      <div className="Nav-Container" style={{ height: navHeight }}>
        <div className="Nav-Toggle">
          <button
            className="Nav-Toggler"
            onClick={() => {
              if (toggled) {
                setToggled(false);
              } else {
                setToggled(true);
              }
            }}
          >
            <span></span>
            <span></span>
            <span></span>
          </button>
          <ul
            className="Nav-Links"
            style={
              window.innerWidth >= 767
                ? toggled
                  ? { display: "flex" }
                  : { display: "none" }
                : { display: "none" }
            }
          >
            <li>Stories</li>
            <li>Works</li>
            <li>Contacts</li>
          </ul>
          <ul
            className="Nav-Links-Mobile"
            style={
              window.innerWidth <= 767
                ? toggled
                  ? { display: "flex" }
                  : { display: "none" }
                : { display: "none" }
            }
          >
            <li>Stories</li>
            <li>Works</li>
            <li>Contacts</li>
          </ul>
        </div>
        {window.innerWidth >= 767 ? <Logo /> : <></>}
      </div>
    </nav>
  );
}
